.single-img, .portfolio-img{
    position: relative;
    display: inline-table;
    height: 100%;
}
.single-img:after, .portfolio-img:after{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    content: "";
    opacity: 0;
    transform: scaleY(0);
    background: rgba(218, 131, 32, 0.594);
    -webkit-transition: all 0.5s ease 0s;
    -o-transition: all 0.5s ease 0s;
    transition: all 0.5s ease 0s;
}
.single-class:hover .single-img:after, .single-class:hover .gallery-icon, .grid-item:hover .portfolio-img:after, .grid-item:hover .gallery-icon, .single-blog:hover .gallery-icon{
    opacity: 1;
    transform: scaleY(1);
}
.gallery-icon {
  bottom: 0;
  height: 40px;
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  text-align: center;
  top: 0;
    opacity: 0;
    z-index: 9;
  transform: scaleY(0);
    -webkit-transition: all 0.5s ease 0s;
    -o-transition: all 0.5s ease 0s;
    transition: all 0.5s ease 0s;
}
.single-img i{
    color :#fff;
    font-size: 35px;
    z-index: 99;
    position: absolute;
    text-align: center;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: table-cell;
    vertical-align: middle;
    background: transparent;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    margin: auto;
    line-height: 42px;
    -webkit-transition: all 0.5s ease 0s;
    -o-transition: all 0.5s ease 0s;
    transition: all 0.5s ease 0s;
}
.single-img i:hover{
    color: rgba(245, 136, 11, 0.907);
    background: #fff;
    transform: scale(1);
}
.single-content{
    background: #fff;
}
.single-content h3{
    font-size: 18px;
    text-transform: capitalize;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
